/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

p, h1, h2, h3, h4, h5, h6, em, div, li, span, strong {
 color: var(--global-text-color);
}

p:last-child {
 margin-bottom: 0 !important;
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table {
 td, th {
 color: var(--global-text-color);
 }
 td {
 font-size: 1rem;
 }
}

a, table.table a {
 // color: var(--global-theme-color);
 color: var(--global-text-color);
 border-bottom: 0.5px dashed var(--global-text-color);
 &:hover {
 color: var(--global-theme-color);
 // text-decoration: underline;
 }
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0.5em 10px;
 font-size: 1.2rem;
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
}

// Card
.card-breaker{
 text-align:center;
 margin-top:1.2em;
 margin-bottom:0.5em;
 font-weight: 500;
 font-size:1.25em;
 // color: var(--global-text-color-light);
}

.card {
 background-color: var(--global-card-bg-color);
 box-shadow: none;
 border-radius: 1rem;
 margin-bottom: 1rem;
 padding: 0.618rem;

 img {
 width: 100%;
 }

 .card-body {
 padding: 0.25rem;
 }

 .card-title {
 font-size: 1.2em;
 font-weight: 600;
 color: var(--global-text-color);
 margin-bottom: 0.05em;
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }

 .card-text {
 margin-bottom: 0.05em;
 }
}



// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile

.profile {
 img{
 width: 150px;
 border-radius: 100%;
 margin: 0;
 }

 .address {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: monospace;
 p {
 display: inline-block;
 margin: 0;
 }
 }
}


.table-responsive {
 margin-bottom: -1rem;
}

// Navbar customization

.navbar {
 box-shadow: none;
 // border-bottom: 1px solid var(--global-divider-color);
 background-color: rgba(0,0,0,0.9);
 opacity: 0.95;
}
.navbar .dropdown-menu {
 background-color: rgba(50,50,50,1);
 margin-top: 0.5em;
 margin-left: -0.5em;
 border-radius: 0;
 border: none;

 a {
 font-weight: 300;
 }
 a:not(.active) {
 color: var(--global-bg-color);
 }
 a:hover {
 color: var(--global-hover-color);
 }
 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
}
.dropdown-item {
 color: var(--global-bg-color);
 &:hover {
 color: var(--global-hover-color);
 background-color: var(--global-text-color);
 }
}
.navbar.navbar-light {
 a {
 &:hover {
 text-decoration: none;
 }
 }
 .navbar-brand {
 color: var(--global-bg-color);
 border: none;
 font-weight: 300;
 }
 .navbar-nav .nav-item .nav-link {
 padding-left: 0px;
 padding-right: 25px;
 color: var(--global-bg-color);
 border: none;
 font-weight: 300;
 &:hover {
 color: var(--global-hover-color);
 font-weight: 300;
 }
 }
 .navbar-nav .nav-item.active>.nav-link {
 background-color: inherit;
 font-weight: 300;
 color: var(--global-theme-color);
 border: none;
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-brand.social {
 padding-bottom: 0;
 padding-top: 0;
 font-size: 1.7rem;
 a {
 border-bottom: 0px;
 i::before {
 color: var(--global-bg-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
 }
 }
}

.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-bg-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }
 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }
 .middle-bar {
 opacity: 0;
 }
 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }
 .middle-bar {
 opacity: 1;
 }
 .bottom-bar {
 transform: rotate(0);
 }
}

// Social (bottom)

.social {
 text-align: left;
 .contact-icons {
 font-size: 1.5rem;
 margin-top: 0.8rem;
 a {
 border-bottom: none;
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
 .contact-note {
 font-size: 0.8rem;
 }
}


// Footer
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);
 font-size: 0.75rem;
 .container {
 color: var(--global-footer-text-color);
 padding-top: 9px;
 padding-bottom: 8px;
 }
 a {
 color: var(--global-footer-link-color);
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 border-top: 1px solid var(--global-divider-color);
 padding-top: 40px;
 padding-bottom: 50px;
 margin: auto;
}


// Blog

.header-bar {
 // border-bottom: 1px solid var(--global-divider-color);
 // padding-top: 1rem;
 max-width: 704px;
 margin: auto;
 margin-bottom: 2.5rem;
}

.post-list {
 margin: 0;
 margin-bottom: 40px;
 padding: 0;
 li {
 // border-bottom: 1px solid var(--global-divider-color);
 list-style: none;
 // padding-top: 2rem;
 // padding-bottom: 2rem;
 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 .post-tags {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 0;
 }
 a {
 color: var(--global-text-color);
 text-decoration: none;
 border: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 .card{
 margin: auto;
 margin-bottom: 1em;
 max-width: 704px;
 .card-body{
 margin-top: auto;
 margin-bottom: auto;
 .col-sm-3{
 margin-top: auto;
 margin-bottom: auto;
 }
 }
 h3{
 font-weight: 500;
 font-size: 1.2em;
 margin: 0;
 }
 }
 }
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color-light);
 &:hover {
 color: $black-color;
 }
 }
 &.active .page-link {
 color: var(--global-theme-color);
 background-color: rgba(1,1,1,0);
 box-shadow: none;
 &:hover {
 background-color: var(--global-card-bg-color);
 }
 }

 }
}

.pagination.pagination-lg .page-item .page-link {
 font-size: 0.9rem;
 border-radius: 50%;
 padding-left: 1.15rem;
 padding-right: 1.15rem;
 margin-left: 0.2rem;
 margin-right: 0.2rem;
}


// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-theme-color);
 text-decoration: none;
 }
}


// Projects

.projects {
 a {
 text-decoration: none;

 &:hover {
 .card-title {
 color: var(--global-theme-color);
 }
 }
 }

 .card-container{
 min-height: 260px;
 }

 .card {
 background-color: var(--global-card-bg-color);
 .card-body{
 height: 100px;
 }
 img {
 width: 100%;
 height: 100px;
 object-fit: cover;
 border-radius: 0.618rem;
 }
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }

 .grid-sizer, .grid-item {
 width: 210px;
 margin-bottom: 0px;
 }

 h4.category {
 color: var(--global-text-color-light);
 border-bottom: 0.5px solid var(--global-text-color-light);
 padding-top: 0.5rem;
 padding-bottom: 0.5rem;
 margin-top: 0.5rem;
 margin-bottom: 1rem;
 margin-right: 0.618rem;
 margin-left: 0.618rem;
 // text-align: right;
 }
}


// Publications

.publications {
 h1 {
 color: var(--global-theme-color);
 font-size: 2rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 }
 h2 {
 margin-bottom: 1rem;
 span {
 font-size: 1.5rem;
 }
 }
 h4.year {
 color: var(--global-text-color-light);
 border-bottom: 0.5px solid var(--global-text-color-light);
 padding-top: 0.5rem;
 padding-bottom: 0.5rem;
 margin-top: 0.5rem;
 margin-bottom: 1rem;
 margin-right: 0.618rem;
 margin-left: 0.618rem;
 // text-align: right;
 }

 .badge{
 font-weight: 600;
 box-shadow: none;
 }
 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 li {
 margin-bottom: 1rem;
 .abbr {
 // height: 2rem;
 // margin-bottom: 0.5rem;
 font-size: 0.9rem;
 abbr {
 display: inline-block;
 // background-color: var(--global-theme-color);
 padding-left: 1rem;
 padding-right: 1rem;
 a {
 color: white;
 &:hover {
 text-decoration: none;
 }
 }
 }
 .award {
 background-color: var(--global-theme-color);
 color: var(--global-bg-color) !important;
 border: 1px solid var(--global-theme-color);
 }
 }
 .title {
 font-weight: 600;
 margin-top: 0.25rem;
 }
 .author {
 a {
 color: var(--global-text-color);
 border-bottom: none;
 // border-bottom: 1px dashed var(--global-theme-color);
 &:hover {
 color: var(--global-theme-color);
 // border-bottom-style: solid;
 // text-decoration: none;
 }
 }
 > em {
 border-bottom: 0.5px solid;
 font-style: normal;
 // font-weight: bold;
 }
 }
 .periodical {
 font-size: 0.8em;
 > em {
 // border-bottom: 1px solid;
 font-style: normal;
 // font-weight: bold;
 }
 }
 .links {
 font-size: 0.8rem;
 a.btn {
 background-color: var(--global-bg-color);
 color: var(--global-theme-color);
 padding-left: 0.3rem;
 padding-right: 0.3rem;
 padding-top: 0.2rem;
 padding-bottom: 0.2rem;
 margin-left: 0rem;
 margin-bottom: 0rem;
 margin-top: 0.2rem;
 margin-right: 0.2rem;
 border-radius: 0.3rem;
 text-transform: lowercase;
 &:hover {
 background-color: var(--global-theme-color);
 color: var(--global-bg-color);
 }
 }
 }
 .hidden {
 font-size: 0.8rem;
 max-height: 0px;
 overflow: hidden;
 text-align: justify;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;

 p {
 font-size: 0.8rem;
 color: var(--global-text-color-light);
 line-height: 1.4em;
 margin: 10px;
 }
 pre {
 line-height: 1.4em;
 padding: 10px;
 }
 }
 .hidden.open {
 margin-top: 10px;
 max-height: 100em;
 border-radius: 1rem;
 background-color: var(--global-card-bg-color);
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;
 }
 div.abstract.hidden {
 border: none;
 }
 div.abstract.hidden.open {
 // border: dashed 1px var(--global-bg-color);
 // border-color: var(--global-text-color);
 }
 }
 }
}

.publications ol.bibliography li .teaser {
 margin: 0;
 position: absolute;
 top: 55%;
 transform: translateY(-50%);
}

.publications ol.bibliography li .abbr abbr {
 color: var(--global-theme-color) !important;
 border: 1px solid var(--global-theme-color);
 padding-left: 0.5rem;
 padding-right: 0.5rem;
 padding-top: 0.3rem;
 padding-bottom: 0.3rem;
 margin-right: 0.5rem;
 top: 8px;
 left: 16px;
 border-radius: 0.32rem;
 height: 1.4rem;
}




.news table.table-sm th, table.table-sm td {
 padding-top: 0rem;
 padding-bottom: 0.2rem;
 font-weight: 400;
}

.news table th {
 color: var(--global-text-color);
 font-size: 1rem;
 width: 25%;
}



.table-responsive {
 display: block;
 width: 100%;
 -webkit-overflow-scrolling: touch;
 overflow-x: visible;
}



@media (min-width: 576px) {
 .profile {
 .address {
 p { display: block; }
 }
 }
}

.post-description {
 padding-bottom: 2rem;
 font-size: 0.875rem;
 a {
 color: inherit;
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}


@media (max-width: 576px) {
 body {
 h1{
 font-weight: 600;
 font-size: 1.9em;
 }

 p {
 font-size: 0.95rem;
 }
 }

 .profile {
 width: 150px;
 margin: auto;

 img{
 border-radius: 100%;
 }

 figure {
 margin: 0 0 0.5rem;
 }

 .address {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: monospace;
 p {
 display: inline-block;
 margin: 0;
 }
 }
 }

 .news table.table-sm th, table.table-sm td {
 font-size: 0.95rem;
 padding-top: 0rem;
 padding-bottom: 0.4rem;
 }

 .news table th {
 font-size: 0.95rem;
 color: var(--global-text-color);
 width: 120px;
 }

 .publications{
 font-size: 0.95em;
 }

 .publications ol.bibliography li .teaser {
 margin: 0;
 position: relative;
 top: 0%;
 transform: translateY(0%);
 }

 .projects {
 .card {
 img {
 height: 100px;
 }
 }

 .grid-sizer, .grid-item {
 width: 48%;
 }
 }




 .clearfix{
 text-align: center;
 }

 .social{
 text-align: center;
 }

 .navbar{
 padding-top: 15px;
 padding-bottom: 15px;
 background-color: rgba(0,0,0,0.95);
 }
 .navbar.navbar-light {
 a {
 }
 .navbar-nav .nav-item {
 margin-right: 20px;
 margin-left: 20px;
 padding: 8px;
 border: none;
 border-bottom: 0.02em solid rgba(255,255,255,0.4) ;
 }
 .nav-item:first-child {
 margin-top: 20px;
 }
 .nav-item:last-child {
 border: none;
 margin-bottom: 20px;
 }
 }

 .navbar.navbar-light .navbar-toggler {
 padding-left: 0;
 }

 .navbar .dropdown-menu {
 position: relative !important;
 margin-top: 0;
 border: none;
 background-color: rgba(0,0,0,0);
 }
}

// Rouge Color Customization
figure.highlight {
 margin: 0 0 1rem;
}

pre {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 6px;
 padding: 6px 12px;
 pre, code {
 background-color: transparent;
 border-radius: 0;
 padding: 0;
 }
}

code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 3px;
 padding: 3px 3px;
}


// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
 transition: all 750ms !important;
 transition-delay: 0 !important;
}

// Extra Markdown style (post Customization)
.post{
 .post-meta{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 .post-tags{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 1rem;
 a {
 color: var(--global-text-color-light);
 text-decoration: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }
 .post-content{
 blockquote {
 border-left: 5px solid var(--global-theme-color);
 padding: 8px;
 }
 }
}
